let currentHookIndex = 0;
let hookDependenciesByIndex = [];

export const restartHooks = () => {
    currentHookIndex = 0;
}

export const useEffect = (fn, currentDeps) => {
    if (hookDependenciesByIndex.length <= currentHookIndex) {
        hookDependenciesByIndex.push(currentDeps);
        fn();
    } else {
        let depsChanged = false;
        const prevDeps = hookDependenciesByIndex[currentHookIndex];
        for (let i = 0; i < prevDeps.length; i++) {
            if (currentDeps[i] !== prevDeps[i]) {
                depsChanged = true;
                break;
            }
        }
        if (depsChanged) {
            hookDependenciesByIndex[currentHookIndex] = currentDeps;
            fn();
        }
    }
    currentHookIndex++;
}
