import { AnimatedPointSet, CONNECTIVITY_TYPE, CONFORM_SHAPE_TYPE } from "../../src/AnimatedPointSet";
import { distance } from "../../src/utils";

const preset = {
    "maxPoints": 100,
    "connectivityType": "COMPLETE_GRAPH",
    "motionType": "DIRECTION_CHANGE",
    "maxVelocity": 0.1,
    "maxOffset": 0,
    "overscaleFactor": 1,
    "strokeWidth": 0.387,
    "strokeColor": "rgba(255,255,255,1)",
    "background": "rgba(0,0,0,1)",
    "mapLineLengthToProps": true,
    "minOpacity": 0.3, // 0.68,
    "maxOpacity": 1,
    "opacityCutoffLength": 0.8, // 0.222,
    "conformToShape": "CIRCLE",
    "conformPointsPercentage": 0.25,
    "conformCircleRadius": 0.4,
    "conformCirclePower": 0,
    "conformCircleStickiness": 0,
    "conformCircleCenterX": 0.3,
    "conformCircleCenterY": 0.5,
    "conformRectangleOffsetX": 0,
    "conformRectangleOffsetY": 0,
    "conformRectangleWidth": 0,
    "conformRectangleHeight": 0,
    "conformCutStickyVertices": false,
    "conformCircleStrokeWidth": 8
};

const canvas = document.querySelector("canvas");
const offscreenCanvas = document.createElement("canvas");

const context = canvas.getContext("2d");
const offscreenContext = offscreenCanvas.getContext("2d");

const animatedPointSet = new AnimatedPointSet();
const animatedPointSetBg = new AnimatedPointSet();

Object.assign(animatedPointSet.params, preset);
Object.assign(animatedPointSetBg.params, preset);
animatedPointSetBg.params.maxPoints = 200;
animatedPointSetBg.params.overscaleFactor = 1.5;
animatedPointSetBg.params.strokeWidth = 1.25;
animatedPointSetBg.params.opacityCutoffLength = 0.075;
animatedPointSetBg.params.minOpacity = 0;
animatedPointSetBg.params.maxOpacity = 1;
animatedPointSetBg.params.conformToShape = CONFORM_SHAPE_TYPE.NONE;
animatedPointSetBg.params.connectivityType = CONNECTIVITY_TYPE.DELAUNAY;

animatedPointSet.randomize("1601033250705k");
animatedPointSetBg.randomize("3");

let w = canvas.width;
let h = canvas.height;
let cx = 0;
let cy = 0;
let r = 0;
let textOpacity = 0.5;

const prerender = () => {
    animatedPointSetBg.draw(offscreenContext);
    offscreenContext.fillStyle = animatedPointSet.state.strokeStyle;
    offscreenContext.beginPath();
    offscreenContext.arc(cx, cy, r, 0, 2 * Math.PI, false);
    offscreenContext.save();
    offscreenContext.clip();
    animatedPointSet.draw(offscreenContext);
    offscreenContext.restore();
};

const render = () => {
    context.drawImage(offscreenCanvas, 0, 0);
    context.font = "bold " + (r / 6) + "px sans-serif";
    context.fillStyle = `rgba(255, 255, 255, ${textOpacity})`;
    context.fillText("demo.002", cx - r / 16, cy + r / 3);
    requestAnimationFrame(render);
};

canvas.addEventListener("mousemove", (event) => {
    textOpacity = 1 - (distance(event.offsetX, event.offsetY, cx - r / 16, cy + r / 3) / h);
});

window.addEventListener("resize", () => {
    w = window.innerWidth;
    h = window.innerHeight;
    cx = animatedPointSet.params.conformCircleCenterX * w;
    cy = animatedPointSet.params.conformCircleCenterY * h;
    r = animatedPointSet.params.conformCircleRadius * Math.min(w, h) +
        animatedPointSet.params.conformCircleStrokeWidth;
    canvas.width = w;
    canvas.height = h;
    canvas.style.width = w + "px";
    canvas.style.height = h + "px";
    offscreenCanvas.width = w;
    offscreenCanvas.height = h;
    prerender();
});

window.dispatchEvent(new Event("resize"));
render();
