import seedrandom from "seedrandom";
import Delaunay from "delaunay-fast";
import { useEffect } from "../src/hooks";

export const CONNECTIVITY_TYPE = {
    DELAUNAY: "DELAUNAY",
    COMPLETE_GRAPH: "COMPLETE_GRAPH",
};

export const MOTION_TYPE = {
    OFFSET_CHANGE: "OFFSET_CHANGE",
    VELOCITY_CHANGE: "VELOCITY_CHANGE",
    DIRECTION_CHANGE: "DIRECTION_CHANGE",
};

export const CONFORM_SHAPE_TYPE = {
    NONE: "NONE",
    CIRCLE: "CIRCLE",
    RECTANGLE: "RECTANGLE",
};

const clamp = (val, lo, hi) =>
    val < lo ? lo : val > hi ? hi : val;

export class AnimatedPointSet {
    constructor() {
        this.rng = seedrandom(Date.now());
        this.tempArray1 = [0, 0];
        this.tempArray2 = [0, 0];
        this.tempArray3 = [0, 0];
        this.params = {
            maxPoints: 100,
            connectivityType: CONNECTIVITY_TYPE.DELAUNAY,
            motionType: MOTION_TYPE.DIRECTION_CHANGE,
            maxVelocity: 0,
            maxOffset: 0,
            overscaleFactor: 1,
            strokeWidth: 0.25,
            strokeColor: "rgba(255, 255, 255, 1)",
            background: "rgba(0, 0, 0, 1)",
            mapLineLengthToProps: false,
            minOpacity: 0,
            maxOpacity: 0,
            opacityCutoffLength: 0,
            conformToShape: CONFORM_SHAPE_TYPE.NONE,
            conformPointsPercentage: 0.25,
            conformCircleRadius: 0.25,
            conformCirclePower: 0.5,
            conformCircleStickiness: 0.05,
            conformCircleCenterX: 0.5,
            conformCircleCenterY: 0.5,
            conformRectangleOffsetX: 0,
            conformRectangleOffsetY: 0,
            conformRectangleWidth: 0,
            conformRectangleHeight: 0,
            conformCutStickyVertices: false,
        };
        this.state = {
            width: 1,
            height: 1,
            aspectRatio: 1,
            pointsCount: -1,
            points: null,
            velocities: null,
            directions: null,
            offsets: null,
            triangles: null,
            fillStyle: null,
            strokeStyle: null,
        };
    }
    randomize(seed) {
        this.rng = seedrandom(seed);
        this.state.pointsCount = Math.floor(3 + this.rng() * this.params.maxPoints - 3);
        this.state.points = Array.from({ length: this.state.pointsCount }, () => [this.rng(), this.rng()]);
        if (this.params.connectivityType === CONNECTIVITY_TYPE.DELAUNAY) {
            this.state.triangles = Delaunay.triangulate(this.state.points);
        }
        this.state.velocities = Array.from({ length: this.state.pointsCount }, () => [0, 0]);
        this.state.directions = Array.from({ length: this.state.pointsCount }, () => 0);
        this.state.offsets = Array.from({ length: this.state.pointsCount }, () => [0, 0]);
    }
    syncParamsToState(context) {
        useEffect(() => {
            if (this.params.connectivityType === CONNECTIVITY_TYPE.DELAUNAY) {
                this.state.triangles = Delaunay.triangulate(this.state.points);
            }
        }, [this.params.connectivityType]);
        useEffect(() => {
            if (typeof this.params.background === "string") {
                this.state.fillStyle = this.params.background;
            } else {
                const w = context.canvas.width;
                const h = context.canvas.height;
                const [x0, y0, x1, y1] = this.params.background.direction;
                const gradient = context.createLinearGradient(x0 * w, y0 * h, x1 * w, y1 * h);
                for (const colorStop of this.params.background.colorStops) {
                    gradient.addColorStop(colorStop[0], colorStop[1]);
                }
                this.state.fillStyle = gradient;
            }
        }, [this.params.background]);
        useEffect(() => {
            if (typeof this.params.strokeColor === "string") {
                this.state.strokeStyle = this.params.strokeColor;
            } else {
                const w = context.canvas.width;
                const h = context.canvas.height;
                const [x0, y0, x1, y1] = this.params.strokeColor.direction;
                let gradient;
                if (this.params.conformToShape === CONFORM_SHAPE_TYPE.CIRCLE) {
                    const r = this.params.conformCircleRadius;
                    const d = r * 2;
                    const offsetX = this.params.conformCircleCenterX - r;
                    const offsetY = this.params.conformCircleCenterY - r;
                    gradient = context.createLinearGradient(
                        (offsetX + x0 * d) * w, (offsetY + y0 * d) * h,
                        (offsetX + x1 * d) * w, (offsetY + y1 * d) * h);
                } else {
                    gradient = context.createLinearGradient(x0 * w, y0 * h, x1 * w, y1 * h);
                }
                for (const colorStop of this.params.strokeColor.colorStops) {
                    gradient.addColorStop(colorStop[0], colorStop[1]);
                }
                this.state.strokeStyle = gradient;
            }
        }, [
            this.params.strokeColor,
            this.params.conformToShape,
            this.params.conformCircleRadius
        ]);
    }
    tick(delta) {
        switch (this.params.motionType) {
            case MOTION_TYPE.OFFSET_CHANGE: {
                for (let i = 0; i < this.state.pointsCount; i++) {
                    const dxSign = this.rng() > 0.5 ? -1 : +1;
                    const dySign = this.rng() > 0.5 ? -1 : +1;
                    const offset = this.state.offsets[i];
                    offset[0] = clamp(offset[0] + dxSign * delta, -this.params.maxOffset, +this.params.maxOffset);
                    offset[1] = clamp(offset[1] + dySign * delta, -this.params.maxOffset, +this.params.maxOffset);
                }
                break;
            }
            case MOTION_TYPE.VELOCITY_CHANGE: {
                for (let i = 0; i < this.state.pointsCount; i++) {
                    const dxSign = this.rng() > 0.5 ? -1 : +1;
                    const dySign = this.rng() > 0.5 ? -1 : +1;
                    const velocity = this.state.velocities[i];
                    velocity[0] = clamp(velocity[0] + dxSign * delta, -this.params.maxVelocity, +this.params.maxVelocity);
                    velocity[1] = clamp(velocity[1] + dySign * delta, -this.params.maxVelocity, +this.params.maxVelocity);
                    const offset = this.state.offsets[i];
                    offset[0] = clamp(offset[0] + velocity[0] * delta, -this.params.maxOffset, +this.params.maxOffset);
                    offset[1] = clamp(offset[1] + velocity[1] * delta, -this.params.maxOffset, +this.params.maxOffset);
                }
                break;
            }
            case MOTION_TYPE.DIRECTION_CHANGE: {
                for (let i = 0; i < this.state.pointsCount; i++) {
                    const ddSign = this.rng() > 0.5 ? -1 : +1;
                    this.state.directions[i] += ddSign * 10 * delta;
                    const alpha = this.state.directions[i];
                    const dx = Math.cos(alpha);
                    const dy = Math.sin(alpha);
                    const offset = this.state.offsets[i];
                    offset[0] = clamp(offset[0] + this.params.maxVelocity * dx * delta, -this.params.maxOffset, +this.params.maxOffset);
                    offset[1] = clamp(offset[1] + this.params.maxVelocity * dy * delta, -this.params.maxOffset, +this.params.maxOffset);
                }
                break;
            }
        }
    }
    draw(context) {
        this.syncParamsToState(context);
        const w = context.canvas.width;
        const h = context.canvas.height;
        this.state.width = w;
        this.state.height = h;
        this.state.aspectRatio = w / h;
        const overscaleDelta = (this.params.overscaleFactor - 1) / 2;
        context.save();
        context.translate(-w * overscaleDelta, -h * overscaleDelta);
        context.scale(this.params.overscaleFactor, this.params.overscaleFactor);
        context.fillStyle = this.state.fillStyle;
        context.fillRect(0, 0, w, h);
        context.lineCap = "round";
        context.lineWidth = this.params.strokeWidth;
        context.strokeStyle = this.state.strokeStyle;
        let conformingPointA = this.tempArray1;
        let conformingPointB = this.tempArray2;
        let conformingPointC = this.tempArray3;
        switch (this.params.connectivityType) {
            case CONNECTIVITY_TYPE.DELAUNAY: {
                for (let i = 0; i < this.state.triangles.length; i += 3) {
                    const ix0 = this.state.triangles[i];
                    const ix1 = this.state.triangles[i + 1];
                    const ix2 = this.state.triangles[i + 2];
                    const p0 = this.state.points[ix0];
                    const p1 = this.state.points[ix1];
                    const p2 = this.state.points[ix2];
                    const off0 = this.state.offsets[ix0];
                    const off1 = this.state.offsets[ix1];
                    const off2 = this.state.offsets[ix2];
                    const isStickyA = this.conformPointToShape(p0, off0, conformingPointA);
                    const isStickyB = this.conformPointToShape(p1, off1, conformingPointB)
                    const isStickyC = this.conformPointToShape(p2, off2, conformingPointC);
                    const renderA = !(isStickyA && this.params.conformCutStickyVertices);
                    const renderB = !(isStickyB && this.params.conformCutStickyVertices);
                    const renderC = !(isStickyC && this.params.conformCutStickyVertices);
                    context.save();
                    context.beginPath();
                    if (renderA && renderB) {
                        context.moveTo(conformingPointA[0], conformingPointA[1]);
                        this.params.mapLineLengthToProps && this.lineLengthToStrokeProps(conformingPointA, conformingPointB, context);
                        context.lineTo(conformingPointB[0], conformingPointB[1]);
                    }
                    if (renderB && renderC) {
                        context.moveTo(conformingPointB[0], conformingPointB[1]);
                        this.params.mapLineLengthToProps && this.lineLengthToStrokeProps(conformingPointB, conformingPointC, context);
                        context.lineTo(conformingPointC[0], conformingPointC[1]);
                    }
                    if (renderC && renderA) {
                        context.moveTo(conformingPointC[0], conformingPointC[1]);
                        this.params.mapLineLengthToProps && this.lineLengthToStrokeProps(conformingPointC, conformingPointA, context);
                        context.lineTo(conformingPointA[0], conformingPointA[1]);
                    }
                    context.stroke();
                    context.restore();
                }
                break;
            }
            case CONNECTIVITY_TYPE.COMPLETE_GRAPH: {
                const n = this.state.points.length;
                for (let i = 0; i < n - 1; i++) {
                    const p0 = this.state.points[i];
                    const off0 = this.state.offsets[i];
                    this.conformPointToShape(p0, off0, conformingPointA);
                    for (let j = i + 1; j < n; j++) {
                        const p1 = this.state.points[j];
                        const off1 = this.state.offsets[j];
                        this.conformPointToShape(p1, off1, conformingPointB);
                        context.beginPath();
                        context.moveTo(conformingPointA[0], conformingPointA[1]);
                        this.params.mapLineLengthToProps && this.lineLengthToStrokeProps(conformingPointA, conformingPointB, context);
                        context.lineTo(conformingPointB[0], conformingPointB[1]);
                        context.stroke();
                    }
                }
                break;
            }
        }
        if (this.params.conformToShape === CONFORM_SHAPE_TYPE.CIRCLE) {
            context.lineWidth = this.params.conformCircleStrokeWidth;
            context.strokeStyle = this.params.strokeColor;
            context.globalAlpha = 1;
            context.beginPath();
            context.arc(
                this.params.conformCircleCenterX * w,
                this.params.conformCircleCenterY * h,
                this.params.conformCircleRadius * Math.min(w, h),
                0, 2 * Math.PI, false);
            context.stroke();
        }
        context.restore();
    };
    conformPointToShape(inputPoint, inputPointOffset, resultPointRef) {
        let isSticky = false;
        const w = this.state.width;
        const h = this.state.height;
        const x = clamp(inputPoint[0] + inputPointOffset[0], 0, 1);
        const y = clamp(inputPoint[1] + inputPointOffset[1], 0, 1);
        switch (this.params.conformToShape) {
            case CONFORM_SHAPE_TYPE.NONE: {
                resultPointRef[0] = x * w;
                resultPointRef[1] = y * h;
                return isSticky;
            }
            case CONFORM_SHAPE_TYPE.CIRCLE: {
                const dx = 0.5 - x;
                const dy = 0.5 - y;
                const alpha = Math.atan2(dy, dx);
                const d = Math.sqrt(dx * dx + dy * dy);
                const dmax = 0.7071067811865476;
                let r = this.params.conformCircleRadius * Math.pow(d / dmax, this.params.conformCirclePower);
                if (Math.abs(r - this.params.conformCircleRadius) < this.params.conformCircleStickiness) {
                    r = this.params.conformCircleRadius;
                    isSticky = true;
                }
                const k = Math.min(w, h);
                resultPointRef[0] = this.params.conformCircleCenterX * w + k * r * Math.cos(alpha);
                resultPointRef[1] = this.params.conformCircleCenterY * h + k * r * Math.sin(alpha);
                return isSticky;
            }
            case CONFORM_SHAPE_TYPE.RECTANGLE:
                return isSticky;
        }
    }
    lineLengthToStrokeProps(p1, p2, context) {
        const dx = p1[0] - p2[0];
        const dy = p1[1] - p2[1];
        const length = Math.sqrt(dx * dx + dy * dy);
        const maxLength = this.params.conformToShape === CONFORM_SHAPE_TYPE.CIRCLE
            ? this.params.conformCircleRadius * 2 * Math.min(this.state.width, this.state.height)
            : Math.sqrt(this.state.width * this.state.width + this.state.height * this.state.height);
        const normLength = clamp(length / maxLength, 0, this.params.opacityCutoffLength) / this.params.opacityCutoffLength;
        const opacity = this.params.minOpacity + (1 - normLength) * (this.params.maxOpacity - this.params.minOpacity);
        context.globalAlpha = opacity;
    }
}
